import router from '@/router'
import Cookies from 'js-cookie'
import { Message } from 'element-ui'

const types = {
  SET_USER_INFO: 'root/SET_USER_INFO',
  SET_DATA_LOADING: 'root/SET_DATA_LOADING'
}
const state = {
  userInfo: null,
  dataLoading: false
}

const getters = {
  userId: state => state.userInfo && state.userInfo.id,
  userName: state => {
    if (state.userInfo && state.userInfo.email) {
      return state.userInfo.email.split('@')[0]
    } else {
      return null
    }
  },
  userRole: state => state.userInfo && Object.values(state.userInfo.roles),
  userType: state => state.userInfo && state.userInfo.type
}

const actions = {
  // 當API發送請求時會被使用， 參照：'src/utils/request'
  setDataLoading ({ commit }, data) {
    commit(types.SET_DATA_LOADING, data)
  },
  setUserInfo ({ commit }, data) {
    commit(types.SET_USER_INFO, data)
  },
  // 登出邏輯 (登入邏輯於 src/views/Login/index.vue 中)
  systemLogout ({ commit }, message) {
    // 登出時先將Cookie, Vuex, sessionStorage的用戶相關資訊移除
    commit(types.SET_USER_INFO, null)
    Cookies.remove('qp_access_token')
    window.localStorage.setItem('systemLogout', new Date())
    window.sessionStorage.removeItem('qpUserInfo')
    router.push({ name: 'Login' })
    Message.warning(message)
  }
}

const mutations = {
  [types.SET_USER_INFO] (state, data) {
    state.userInfo = data
  },
  [types.SET_DATA_LOADING] (state, data) {
    state.dataLoading = data
  }
}

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations
}
