import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      // 非routes設定的的path, 一律導向'/'
      path: '*',
      redirect: '/'
    },
    {
      // 登入頁
      name: 'Login',
      path: '/',
      component: () => import('@/views/Login/Index'),
      meta: { title: 'Quick Plan - 登入' }
    },
    {
      name: 'Index',
      path: '/',
      redirect: { name: 'Calculation' },
      component: () => import('@/views/Main'),
      meta: { requiresAuth: true },
      children: [
        {
          // Calculation route
          path: 'calculation',
          component: () => import('@/views/Calculation/Index'),
          children: [
            {
              name: 'Calculation',
              path: '',
              component: () => import('@/views/Calculation/Index'),
              meta: { title: '計算頁面' }
            }
          ]
        },
        {
          // Compare route
          path: 'compare',
          component: () => import('@/views/Compare/Index'),
          children: [
            {
              name: 'Compare',
              path: '',
              component: () => import('@/views/Compare/Index'),
              meta: { title: '結果比較' }
            }
          ]
        }
      ]
    }
  ]
})
