import $lodash from 'lodash'

const types = {
  SET_OPTION_DATA: 'calculation/SET_OPTION_DATA',
  SET_DATA_SOURCE: 'calculation/SET_DATA_SOURCE',
  UPDATE_SELECTED_DATA: 'calculation/UPDATE_SELECTED_DATA',
  RESET_SELECTED_DATA: 'calculation/RESET_SELECTED_DATA',
  UPDATE_REQUIRE_CHECK_DATA: 'calculation/UPDATE_REQUIRE_CHECK_DATA',
  ADD_COMPARE_DATA: 'calculation/ADD_COMPARE_DATA',
  REMOVE_COMPARE_DATA: 'calculation/REMOVE_COMPARE_DATA',
  TOGGLE_COMPARE_CHART_DISPLAY: 'calculation/TOGGLE_COMPARE_CHART_DISPLAY'
}

// 預設的空選項
const defaultOptions = () => {
  return {
    flv_second: [],
    freq: 2,
    demographics: {
      male: [],
      female: []
    },
    hourlies: [],
    interests: [],
    keywords: [],
    medias: {
      categories: []
    },
    cities: {
      regions: [],
      municipalities: []
    },
    isps: [],
    device_brands: []
  }
}

// 預設的空選擇資料
const defaultSelectedData = () => {
  return {
    baseInfo: {
      name: null,
      clientId: null,
      clientName: null,
      agencyId: null,
      agencyName: null
    },
    playDate: null,
    flvSecond: null,
    frequency: null,
    demographics: null,
    hourlies: null,
    interestKeyword: {
      type: null,
      interestIds: null,
      keywordIds: null
    },
    media: {
      isPrime: null,
      includeIds: null,
      excludeIds: null
    },
    cities: {
      regions: null,
      municipalities: null
    },
    isps: null,
    deviceBrands: null,
    budgetReach: null
  }
}

// 選項必填檢查
const defaultRequireCheckList = () => {
  return {
    flvSecond: false,
    frequency: false,
    demographics: false,
    hourlies: false,
    interestKeyword: false,
    media: false,
    cities: false,
    isps: false,
    deviceBrands: false,
    budgetReach: false,
    playDate: false,
    baseInfo: true
  }
}

const state = {
  options: defaultOptions(),
  dataSource: defaultSelectedData(),
  selectedData: defaultSelectedData(),
  requireCheckList: defaultRequireCheckList(),
  compareDataLimit: 10, // 比較上限
  compareDataList: []
}

const getters = {
  // 檢查所有選項是否都通過必填檢驗
  requireAllPass: state => Object.values(state.requireCheckList).every(warningText => !warningText),
  // 判斷是否已達比較上限
  isCompareDataLimit: state => state.compareDataList.length >= state.compareDataLimit
}

const actions = {
  // 設定選項
  setOptions ({ commit }, data) {
    commit(types.SET_OPTION_DATA, data)
  },
  // 設定條件組合資料
  setDataSource ({ commit }, data) {
    commit(types.SET_DATA_SOURCE, data)
  },
  // 更新選擇資料
  updateSelectedData ({ commit }, data) {
    commit(types.UPDATE_SELECTED_DATA, data)
  },
  // 清空選擇資料
  resetSelectedData ({ commit }) {
    commit(types.RESET_SELECTED_DATA)
  },
  // 更新欄位檢查狀態
  updateRequireCheckData ({ commit }, data) {
    commit(types.UPDATE_REQUIRE_CHECK_DATA, data)
  },
  // 新增比較
  addCompareData ({ commit }, data) {
    commit(types.ADD_COMPARE_DATA, data)
  },
  // 移除比較
  removeCompareData ({ commit }, index) {
    commit(types.REMOVE_COMPARE_DATA, index)
  },
  // 切換該index比較圖表顯示/隱藏
  toggleCompareChartDisplay ({ commit }, index) {
    commit(types.TOGGLE_COMPARE_CHART_DISPLAY, index)
  }
}

const mutations = {
  [types.SET_OPTION_DATA] (state, data) {
    Object.assign(state.options, data)
  },
  [types.SET_DATA_SOURCE] (state, data) {
    state.dataSource = JSON.parse(data)
  },
  [types.UPDATE_SELECTED_DATA] (state, data) {
    const cloneData = $lodash.cloneDeep(data)
    cloneData.forEach(item => {
      $lodash.set(state.selectedData, item.key, item.value)
    })
  },
  [types.RESET_SELECTED_DATA] (state) {
    state.dataSource = defaultSelectedData()
  },
  [types.UPDATE_REQUIRE_CHECK_DATA] (state, data) {
    const cloneData = $lodash.cloneDeep(data)
    state.requireCheckList[cloneData.key] = cloneData.value
  },
  [types.ADD_COMPARE_DATA] (state, data) {
    data.display = true
    state.compareDataList.push(data)
  },
  [types.TOGGLE_COMPARE_CHART_DISPLAY] (state, index) {
    state.compareDataList[index].display = !state.compareDataList[index].display
  },
  [types.REMOVE_COMPARE_DATA] (state, index) {
    state.compareDataList.splice(index, 1)
  }
}

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations
}
